.form {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin: 0 auto 10px;
  padding: 0 30px;
  font-size: 14px;

  &__label {
    color: rgba(220, 200, 170);
    font-size: 14px;
    margin-bottom: 3px;
    padding: 2px 5px 0 5px;
  }

  &__input {
    width: 100%;
    margin-bottom: 10px;
    font-size: 14px;
    font-family: "Montserrat", sans-serif;
    border: none;
    line-height: 20px;
    padding: 2px 5px;
    background-color: rgba(255, 255, 255, 0.85);
    z-index: 160;
    transition: all 0s;

    &__notActive {
      @extend .form__input;
      z-index: 1;
    }
  }

  &__textarea {
    font-family: "Montserrat", sans-serif;
    font-size: 14px;
    margin-bottom: 10px;
    line-height: 18px;
    height: 170px;
    resize: none;
    border: none;
    padding: 2px 5px;
    background-color: rgba(255, 255, 255, 0.85);
    z-index: 160;
    transition: all 0s;

    &__notActive {
      @extend .form__textarea;
      z-index: 1;
    }
  }

  &__btn {
    width: 100px;
    margin: 0 auto 20px;
    font-size: 14px;
    border: 1px solid rgba(255, 255, 255);
    border-radius: 6%;
    transition: 0.4s ease;
    text-transform: uppercase;
    font-family: "Montserrat", sans-serif;
    background-color: rgba(255, 255, 255, 0.85);
    color: rgb(15, 15, 15);

    &:hover,
    &:focus {
      background-color: rgb(15, 15, 15);
      color: #d3b058;
      font-weight: 700;
      border: 1px solid rgb(15, 15, 15);
    }
  }

  &__textarea:focus,
  &__input:focus {
    outline: none;
  }
  &::placeholder {
    font-family: "Montserrat", sans-serif;
  }
}

@media (min-width: 600px) {
  .form {
    width: 70%;
  }
}
@media (min-width: 800px) {
  .form {
    width: 60%;
  }
}
@media (min-width: 1200px) {
  .form {
    width: 40%;
    font-size: 28px;

    &__label {
      font-size: 18px;
      margin-bottom: 5px;
    }

    &__input {
      margin-bottom: 10px;
      font-size: 18px;
      font-family: "Montserrat", sans-serif;
      line-height: 24px;
      padding: 2px 5px;
    }

    &__textarea {
      font-size: 18px;
      line-height: 24px;
      height: 250px;
    }

    &__btn {
      font-size: 18px;
      padding: 5px 10px;
    }
  }
}
